.man {
	width: 100%;
}

.product-sort {
	width: 100%;
	margin: 0 auto;
	border-bottom: 1px solid black;
}

.product-sort ul {
	width: 40%;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
}

.product-sort ul li {
	line-height: 40px;
	font-weight: 600;
	width: 100px;
	text-align: center;
}

.man-banner {
	width: 100%;
	height: 300px;
}

.man-banner h2 {
	width: 100%;
	height: 300px;
	line-height: 300px;
	text-align: center;
	font-size: 50px;
	font-weight: 650;
}

.center {
	width: 90%;
	height: auto;
	margin: 0 auto;
}

.center .sort {
	width: 100%;
	height: 30px;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
}

.center .sort ul {
	display: flex;
	line-height: 30px;
	justify-content: flex-start;
}

.center .sort ul li {
	padding: 0 10px;
}

.center .product {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	flex-flow: row wrap;
}

.center .product .product-detail {
	width: 20%;
	height: 550px;
	padding: 20px 10px;
	box-sizing: border-box;
	position: relative;
	margin-bottom: 50px;
}

.center .product .product-detail .img-box {
	width: 100%;
	height: 400px;
	border-radius: 10px;
	overflow: hidden;
	box-sizing: border-box;
}

.center .product .product-detail .img-box img {
	width: 100%;
	height: 400px;
	object-fit: cover;
	transform: scale(1.05);
}

.center .product .product-detail h3 {
	margin-top: 10px;
	font-size: 20px;
}
.center .product .product-detail span {
	position: absolute;
	bottom: 20px;
	left: 10px;
}
