* {
	margin: 0;
	padding: 0;
}

ul,
ol {
	list-style: none;
}

a {
	text-decoration: none;
}

#wrap {
	width: 100%;
	height: auto;
}

#wrap .main-banner {
	width: 100%;
	height: 1000px;
	background: url(/public/img/bg_01.jpg) no-repeat center;
	background-size: cover;
}

#sub-banner {
	padding: 50px 0 0 0;
	width: 90%;
	margin: 0 auto;
	display: flex;
	min-width: 1500px;
	max-width: 1700px;
	overflow: hidden;
}

#sub-banner .sub-banner-01 {
	width: 100%;
	height: 100px;
	background: url(/public/img/sub-banner-01.jpg) no-repeat center;
	background-size: cover;
	position: relative;
	min-width: 1500px;
	max-width: 1700px;
}
#sub-banner .sub-banner-01 h3 {
	font-size: 25px;
	position: absolute;
	left: 200px;
	top: 30px;
}

#sub-banner .sub-banner-01 p {
	position: absolute;
	left: 200px;
	top: 60px;
}

#sub-banner .sub-banner-02 {
	width: 100%;
	height: 100px;
	background: url(/public/img/sub-banner-02.jpg) no-repeat center;
	background-size: cover;
	position: relative;
	min-width: 1500px;
	max-width: 1700px;
}

#sub-banner .sub-banner-02 h3 {
	font-size: 25px;
	position: absolute;
	left: 200px;
	top: 30px;
}

#sub-banner .sub-banner-02 p {
	position: absolute;
	left: 200px;
	top: 60px;
}

#wrap #best {
	width: 100%;
}

.center {
	width: 90%;
	min-width: 1500px;
	max-width: 1700px;
	margin: 0 auto;
}

#wrap #best .center h2 {
	width: 100%;
	text-align: center;
	font-size: 50px;
	padding-top: 50px;
	padding-bottom: 10px;
	font-weight: 600;
}
#wrap #best .center ul {
	padding-top: 10px;
	width: 200px;
	height: 50px;
	line-height: 50px;
	display: flex;
	margin: 0 auto;
	justify-content: space-around;
	font-size: 40px;
	font-weight: 500;
}

#wrap #best .center ul li {
	cursor: pointer;
}

#wrap #best .center .product-man {
	padding-top: 20px;
	padding-bottom: 50px;
	width: 100%;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}

#wrap #best .center .product-man .product-detail {
	width: 25%;
	height: 650px;
	position: relative;
	padding: 20px;
	box-sizing: border-box;
}

#wrap #best .center .product-man .product-detail .img-box {
	width: 100%;
	height: 500px;
	border-radius: 10px;
	overflow: hidden;
}
#wrap #best .center .product-man .product-detail .img-box img {
	height: 100%;
	width: 100%;
	object-fit: cover;
	transition: all 0.5s;
}

#wrap #best .center .product-man .product-detail .img-box img:hover {
	transform: scale(1.1);
}
#wrap #best .center .product-man .product-detail span {
	position: absolute;
	bottom: 20px;
}

#wrap #best .center .product-man .product-detail h3 {
	font-size: 19px;
	margin-top: 10px;
	font-weight: 500;
}

#wrap #best .center .product-woman {
	padding-top: 20px;
	padding-bottom: 50px;
	width: 100%;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
}

#wrap #best .center .product-woman .product-detail {
	width: 25%;
	height: 650px;
	position: relative;
	padding: 20px;
	box-sizing: border-box;
}

#wrap #best .center .product-woman .product-detail .img-box {
	width: 100%;
	height: 500px;
	border-radius: 10px;
	overflow: hidden;
}
#wrap #best .center .product-woman .product-detail .img-box img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	transition: all 0.5s;
}

#wrap #best .center .product-woman .product-detail .img-box img:hover {
	transform: scale(1.1);
}
#wrap #best .center .product-woman .product-detail span {
	position: absolute;
	bottom: 10px;
}

#wrap #best .center .product-woman .product-detail h3 {
	width: 100;
	font-size: 19px;
	margin-top: 10px;
}

.selected {
	border-bottom: 2px solid #333;
}

#seen-on-tv {
	height: auto;
	padding: 50px 0;
	display: flex;
}

#seen-on-tv .left {
	width: 50%;
	/* border-radius: 10px; */
	overflow: hidden;
	padding-right: 10px;
}

#seen-on-tv .left img {
	width: 100%;
	height: 100%;
	transform: scale(1.05);
	object-fit: cover;
}

#seen-on-tv .right {
	width: 50%;
	padding-left: 10px;
	display: flex;
}

#seen-on-tv .right .img-box {
	border-radius: 10px;
	overflow: hidden;
	box-sizing: border-box;
	padding: 0 10px;
	width: 50%;
}

#seen-on-tv .right .img-box img {
	width: 100%;
	object-fit: cover;
}

#brand-look-book {
	width: 100%;
}

#brand-look-book ul {
	width: 40%;
	height: 100px;
	line-height: 100px;
	display: flex;
	justify-content: space-around;
	margin: 0 auto;
}

#brand-look-book ul li a {
	color: black;
	height: 50px;
	line-height: 50px;
	width: 200px;
	border: 1px solid #000;
	border-radius: 25px;
	display: block;
	text-align: center;
}

.look-book {
	width: 80%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.look-book div {
	width: 50%;
	padding: 20px;
}

.look-book div img {
	width: 100%;
}

.border {
	color: #fff !important;
	background-color: black;
}
