.collection-wrap {
	width: 100%;
	height: auto;
	min-width: 1500px;
	max-width: 1700px;
	padding: 50px 0;
}

.collection-wrap .center {
	width: 90%;
	margin: 0 auto;
	text-align: center;
}

.collection-wrap .center h3 {
	width: 100%;
	padding: 0 0 40px 0;
	line-height: 60px;
	font-size: 40px;
}

.collection-wrap .center .collection {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	flex-flow: row wrap;
}
.collection-wrap .center .collection .detail {
	width: 25%;
	height: 510px;
	overflow: hidden;
}

.collection-wrap .center .collection .detail img {
	width: 100%;
	height: 510px;
	transition: all 0.5;
	padding: 20px;
	box-sizing: border-box;
	overflow: hidden;
	border-radius: 20px;
}

.collection-wrap .center .collection .detail img:hover {
	transform: scale(1.1);
}
