footer {
	width: 100%;
	height: 450px;
	background-color: black;
}

.center .title {
	width: 100%;
	height: 250px;
	border-bottom: 1px solid white;
}

.center .title h1 {
	width: 100%;
	height: 100%;
	color: white;
	text-align: center;
	font-size: 100px;
	line-height: 250px;
}

.center .sort {
	width: 100%;
	display: flex;
	justify-content: space-between;
}
.center .sort li p {
	color: white;
	width: 200px;
	text-align: center;
	line-height: 40px;
	font-weight: 800;
}

.copy {
	width: 100%;
}

.copy p {
	width: 100%;
	text-align: center;
	color: white;
	line-height: 150px;
	font-size: 20px;
}
